import {
  SET_STATUS,
  SET_ROLE,
  SET_USERID,
  SET_USER_NAME,
  SET_AVATAR,
  SET_DESCRIPTION,
  SET_PROJECT_NUM,
  SET_FOLLOWERS,
  SET_FOLLOWING,
  SET_ROLES,
  SET_TOKEN,
  SET_CAPTCHA,
} from './mutation-types';
import {
  enterpriseSignIn,
  studentSignIn,
  studentSignInFirstlySendEmail,
  studentSignInFirstlyEmailVerified,
  studentModifyPassword,
  enterpriseSignUpForm,
  enterpriseSignUpV,
  enterpriseSignUpC,
} from '@/api/user';
import { setToken, getToken, removeToken, setUserId, removeUserId, getUserId } from '@/utils/auth';
import router from '@/router';
import { setStore } from '@/utils/storage';
import { Message } from 'element-ui';


/**
 * 
 * 1. state                                           getter
 * 2. roles
 * 3. userInfostatecookie
 * 4. mutations
 * 5. token                          https://juejin.im/post/591aa14f570c35006961acac
 * 6. 
 * 7. ?
 */

const user = {
  state: {
    signIn: false,
    role: 'passer',
    userInfo: {
      userId: '',
      userName: '',
      avatar: '',
      description: '',
      projectNum: '',
      followers: '',
      following: '',
      roles: [],
      token: getToken(),
    },
    captcha: false,    // 
  },
  mutations: {
    [SET_STATUS](state) {
      state.signIn = !state.signIn;
    },
    [SET_ROLE](state, role) {
      state.role = role;
    },
    [SET_USERID](state, userId) {
      state.userInfo.userId = userId;
    },
    [SET_USER_NAME](state, userName) {
      state.userInfo.userName = userName;
    },
    [SET_AVATAR](state, avatar) {
      state.userInfo.avatar = avatar;
    },
    [SET_DESCRIPTION](state, description) {
      state.userInfo.description = description;
    },
    [SET_PROJECT_NUM](state, projectNum) {
      state.userInfo.projectNum = projectNum;
    },
    [SET_FOLLOWERS](state, followers) {
      state.userInfo.followers = followers;
    },
    [SET_FOLLOWING](state, following) {
      state.userInfo.following = following;
    },
    [SET_ROLES](state, roles) {
      state.userInfo.roles = roles;
    },
    [SET_TOKEN](state, token) {
      state.userInfo.token = token;
    },
    [SET_CAPTCHA](state) {
      state.captcha = !state.captcha;
    }
  },
  actions: {
    // 
    // 
    SignIn: function ({ commit, dispatch }, data) {
      if (data.role === 'enterprise') {
        return new Promise((resolve, reject) => {
          enterpriseSignIn(data.userData).then(
            res => {
              if (res.status === 500) {
                Message({
                  type: 'error',
                  message: ''
                });
              } else {
                const token = res.msg;
                commit('SET_STATUS');
                commit('SET_ROLE', data.role);
                commit('SET_TOKEN', token);
                commit('SET_USERID', res.data);
                setUserId(res.data);
                setToken(token);
                setStore('token', token);
                router.push({
                  name: 'EnterpriseHomepage',
                });
                resolve();
              }
            }
          ).catch(err => {
            console.log('');
            reject(err);
          })
        });
      } else {
        return new Promise((resolve, reject) => {
          studentSignIn(data.userData).then(
            res => {
              if (res.status === 500 || res.status === 1100) {  // status1100 id
                Message({
                  type: 'error',
                  message: ''
                });
              } else {
                commit('SET_ROLE', data.role);
                const token = res.msg;
                commit('SET_STATUS');
                commit('SET_TOKEN', token);
                commit('SET_USERID', res.data);
                setUserId(res.data);
                setToken(token);
                if (res.status === 1111) {   //  1111 
                  router.push({
                    name: 'SignInFirst',
                  });
                  resolve();
                } else {
                  router.push({
                    name: 'StudentHomepage',
                  });
                  resolve();
                }
              }
            }
          ).catch(err => {
            reject(err);
          })
        });
      }
    },

    // 
    StudentSignInFirstlySendEmail: function ({ commit }, data) {
      return new Promise((resolve, reject) => {
        studentSignInFirstlySendEmail(data).then(res => {
          Message({
            type: 'success',
            message: ''
          });
          commit('SET_CAPTCHA');
          resolve();
        }).catch(err => {
          reject('');
        });
      });
    },

    // 
    SignInFirst: function ({ commit }, data) {
      if (user.state.captcha) {
        return new Promise((resolve, reject) => {
          studentSignInFirstlyEmailVerified(data).then(res => {
            router.push({
              name: 'SignIn',
            });
            resolve();
          }).catch(err => {
            Message({
              type: 'error',
              message: ''
            });
          });
        });
      } else {
        console.log('');
      }
    },
    // 
    SignOut: function ({ commit }) {
      return new Promise((resolve) => {
        commit('SET_TOKEN', '');
        commit('SET_ROLES', 'passer');
        removeToken();
        resolve();
      });
    },

    /***
     * 
     * 1. data
     * 2. 3
     * 3. 
     */


    // 
    VerifyCaptcha: function ({ state, dispatch }, data) {
      console.log('')
      return new Promise((resolve, reject) => {
        enterpriseSignUpV(data.captcha).then(res => {
          dispatch('SignUp', data.userData);
          resolve();
        }).catch(err => {
          console.log('');
          reject(err);
        });
      });
    },

    //,
    SignUp: function (data) {
      return new Promise((resolve, reject) => {
        enterpriseSignUpForm(data).then(res => {
          console.log(res);
          console('');
          resolve();
        }).catch(err => {
          console.log(err);
          reject(err);
        });
      });
    },

    // 
    ChangeRoles({ commit, dispatch }, role) {
      return new Promise((resolve, reject) => {
        commit('SET_TOKEN', role);
        setToken(role);
        getUserInfo(role).then(res => {
          const data = res.data;
          commit('SET_ROLES', data.roles);
          commit('SET_NAME', data.name);
          commit('SET_AVATAR', data.avatar);
          commit('SET_DESCRIPTION', data.description);
          dispatch('GenerateRoutes', data);
          resolve();
        }).catch(err => {
          reject(err);
        });
      }).catch(err => {
        reject(err);
      });
    }
  },
};

export default user;



