<template >
  <div class="container-la" v-if="!user.signIn">
    <div class="left">
      {{ content }}
    </div>
    <div class="right">
      <button class="signup-button" @click="goToSignUp()"></button>
    </div>
    <!-- <svg-icon icon="delete-icon" @click="goToDelete()" class="delete"></svg-icon> -->
  </div>
</template>

<script>
/**
 * 
 * 
 *   1.                                                         
 * 
 *   1.                                          
 */
import { mapGetters } from 'vuex';

export default {
	data() {
		return {
			content: '',
			delete: false,
		};
	},
	computed: {
		...mapGetters(['user']),
	},
	methods: {
		goToSignUp() {
			this.$router.push({
        name: 'SignUp',
      });
		},
	},
};
</script>

<style lang="scss" scoped>
@import 'src/assets/scss/index';

.container-la {
	@include wh(100%, $h-regBtn);
	background-color: $clr-content;
	display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  border: $border;
  > div {
    margin-top: 1.5em;
  }
	.left {
		position: relative;
		color: #333333;
	}
	.signup-button {
		height: 2rem;
		background-color: $clr-main;
		color: $clr-white;
    border-radius: 5px;
    cursor: pointer;
	}
}
</style>




